<!-- components/VideoForm.vue -->
<template>
    <form @submit.prevent="processVideo">
      <input v-model="url" type="url" placeholder="Enter YouTube URL" required>
      <button type="submit" :disabled="pending">Process Video</button>
    </form>
  </template>
  
  <script setup lang="ts">
  const url = ref('')
  const pending = ref(false)
  const router = useRouter()
  
  async function processVideo() {
    pending.value = true
    try {
      const formData = new FormData()
      formData.append('url', url.value)
      const { video_id } = await $fetch<{ video_id: string }>('/api/process-video', {
        method: 'POST',
        body: formData
      })
      router.push(`/video/${video_id}`)
    } catch (error) {
      console.error('Error processing video:', error)
      // Handle error (e.g., show an error message to the user)
    } finally {
      pending.value = false
    }
  }
  </script>