<template>
    <div>
      <h2>Recent Videos</h2>
      <div v-if="pending">Loading...</div>
      <div v-else-if="error">{{ error.message }}</div>
      <div v-else-if="videos">
        <div v-for="video in videos" :key="video.video_id">
          <h3>
            <NuxtLink :to="`/video/${video.video_id}`">{{ video.title }}</NuxtLink>
          </h3>
          <p>By {{ video.author }}</p>
          <img :src="video.thumbnail_url" :alt="video.title">
        </div>
      </div>
    </div>
  </template>
  
  <script setup lang="ts">
import type { Video } from '../types'  
  const { data: videos, pending, error } = await useFetch<Video[]>('/api/videos')
  </script>